import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import {
  getPracticeTypeNo,
  getPracticeClassificationNo,
} from 'services/quicklink';
import routeConstant from 'routes/route/routeConstant';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Loại rèn luyện',
    service: getPracticeTypeNo,
    url: routeConstant.REN_LUYEN__LOAI_REN_LUYEN,
  },
  {
    title: 'XL rèn luyện',
    service: getPracticeClassificationNo,
    url: routeConstant.REN_LUYEN__XEP_LOAI_REN_LUYEN,
  },
];

class DashboardTraining extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardTraining {...props} />
  </Page>
);



